import axios from 'axios';
import { IItemView } from '../models/IItemView';
import { ICategoryView } from '../models/ICategoryView';

export class ItemService{
    private static serverUrl:string = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";
     /**
     @usage : to get all items
     @method : GET
     @body : no-params
     @url : http://localhost:9000/items
     */
    public static getAllItems():Promise<{data:IItemView[]}>{
        const dataUrl = `${this.serverUrl}/items`;
        return axios.get(dataUrl)
    };
    /**
     @usage : get a item
     @method : GET
     @body : no-params
     @url : http://localhost:9000/items/:itemId
     */
     public static getItem(itemId: string): Promise<{ data: IItemView }> {
        const dataUrl = `${this.serverUrl}/items/${itemId}`;
        return axios.get(dataUrl);
   };
    /**
     @usage : create a item
     @method : POST
     @body : name, imageUrl, price, brand, description, categoryId
     @url : http://localhost:9000/items/
     */
    public static createItem(item:IItemView):Promise<{data:IItemView}>{
        const dataUrl = `${this.serverUrl}/items/`;
        return axios.post(dataUrl,item)
    };
    /**
     @usage : Update a item
     @method : PUT
     @body : name, imageUrl, price, brand, description, categoryId
     @url : http://localhost:9000/items/:itemId
     */
    public static updateItem(item:IItemView, itemId:string):Promise<{data:IItemView}>{
      const dataUrl = `${this.serverUrl}/items/${itemId}`;
      return axios.put(dataUrl,item)
    };
     /**
     @usage : Delete a item
     @method : DELETE
     @body : no-params
     @url : http://localhost:9000/items/:itemId
     */
    public static deleteItem(itemId:string):Promise<{data:{}}>{
        const dataUrl = `${this.serverUrl}/items/${itemId}`;
        return axios.delete(dataUrl)
    };
    /**
     @usage : Get all categories
     @method : GET
     @body : no-params
     @url : http://localhost:9000/categories/
     */
    public static getAllCategories():Promise<{data:ICategoryView[]}>{
        const dataUrl = `${this.serverUrl}/categories`;
        return axios.get(dataUrl)
    };
    /**
     @usage : Get a category
     @method : GET
     @body : no-params
     @url : http://localhost:9000/categories/:categoryId
     */
    public static getCategory(categoryId:string):Promise<{data:ICategoryView}>{
        const dataUrl = `${this.serverUrl}/categories/${categoryId}`;
        return axios.get(dataUrl)
    };
}