import { IItemView } from "../../modules/shopping/models/IItemView";
import { ICategoryView } from "../../modules/shopping/models/ICategoryView";
import {createSlice,isRejectedWithValue} from "@reduxjs/toolkit";
import * as shoppingActions from "./shopping.actions";

export const shoppingFeatureKey = "shoppingFeature";

export interface InitialState{
    loading : boolean;
    error : string | null;
    items : IItemView[];
    item : IItemView;
    categories : ICategoryView[];
    category : ICategoryView;
}

const initialState:InitialState = {
    loading : false,
    error : null,
    items : [] as IItemView[],
    item : {} as IItemView,
    categories : [] as ICategoryView[],
    category : {} as ICategoryView
}

export const shoppingSlice = createSlice({
    name : 'shoppingSlice',
    initialState : initialState,
    reducers : {},
    extraReducers : (builder)  => {
        //getAllItemsAction
    builder.addCase(shoppingActions.getAllItemsAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.getAllItemsAction.fulfilled,(state,action) => {
        state.loading = false;
        state.items = action.payload;
    }).addCase(shoppingActions.getAllItemsAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    //getItemAction
    builder.addCase(shoppingActions.getItemAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.getItemAction.fulfilled,(state,action) => {
        state.loading = false;
        state.item = action.payload;
    }).addCase(shoppingActions.getItemAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    //createItemAction
    builder.addCase(shoppingActions.createItemAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.createItemAction.fulfilled,(state,action) => {
        state.loading = false;
        state.item = action.payload;
    }).addCase(shoppingActions.createItemAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    //updateItemAction
    builder.addCase(shoppingActions.updateItemAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.updateItemAction.fulfilled,(state,action) => {
        state.loading = false;
        state.item = action.payload;
    }).addCase(shoppingActions.updateItemAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    //deleteItemAction
    builder.addCase(shoppingActions.deleteItemAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.deleteItemAction.fulfilled,(state,action) => {
        state.loading = false;
        state.item = {} as IItemView;
    }).addCase(shoppingActions.deleteItemAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
       //getAllCategoriesAction
       builder.addCase(shoppingActions.getAllCategoriesAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.getAllCategoriesAction.fulfilled,(state,action) => {
        state.loading = false;
        state.categories = action.payload;
    }).addCase(shoppingActions.getAllCategoriesAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    //getCategoryAction
    builder.addCase(shoppingActions.getCategoryAction.pending,(state,action) => {
        state.loading = true;
    }).addCase(shoppingActions.getCategoryAction.fulfilled,(state,action) => {
        state.loading = false;
        state.category = action.payload;
    }).addCase(shoppingActions.getCategoryAction.rejected,(state,action) => {
        state.loading = false;
        if(isRejectedWithValue(action)){
        state.error = action.payload.message
        }
    })
    }
})