import {combineReducers} from "@reduxjs/toolkit";
import * as shoppingReducer from "./shopping/shopping.reducer";
import * as userReducer from "./users/user.reducer";

const rootReducer: any = combineReducers({
   [shoppingReducer.shoppingFeatureKey]:shoppingReducer.shoppingSlice.reducer,
   [userReducer.userFeatureKey]: userReducer.userSlice.reducer,
});
export default rootReducer;