import React from 'react';
import { Link } from 'react-router-dom';
import imageLogo from "F:/React-JS-Typescript/first-app/src/assets/img/ecommerce-website-logo.png";
import {TokenUtil} from "../util/TokenUtil";
import * as userReducer from "../redux/users/user.reducer";
import {useSelector} from "react-redux";
import {AppDispatch, RootState, useAppDispatch} from "../redux/store";
import {logOutUserAction} from "../redux/users/user.reducer";

const NavBar:React.FC= () => {
    const dispatch: AppDispatch = useAppDispatch();

    // get user state from redux
    const userState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {user: userReduxState} = userState;

    const clickLogOff = () => {
        dispatch({
            type: `${logOutUserAction}`,
            payload: {}
        });
    };
    return (
        <>
            <nav className="navbar bg-warning navbar-expand-sm fixed-top">
            <div className="container">
                {/* <Link className="navbar-brand text-success"to="/">
                <img src={imageLogo}alt=""className="image"/>
                <span className="fw-bold head1pe-1"> Get</span><span className="text-primary fw-bold"> Shopping</span>
                </Link> */}
                <div className="navbar-collapse collapse">
                    <Link to="/">
                    <img src={imageLogo}alt=""className="image"/>
                    </Link>
                    <>
                    {
                        TokenUtil.isLoggedIn() &&  
                    <ul className="navbar-nav me-auto">
                    <li className="nav-item">
                    <Link className="nav-link text-dark fw-bold"to="/shopping/display">Display</Link>
                    </li>
                    <li className="nav-item">
                    <Link className="nav-link text-dark fw-bold"to="/shopping/admin">Admin</Link>
                    </li>
                    </ul>
                    }
                    </>
                    <ul className="navbar-nav ms-auto">
                    {
                        TokenUtil.isLoggedIn() ? <>
                            {
                                userReduxState && Object.keys(userReduxState).length > 0 &&
                                <li className="nav-item">
                                    <Link to="/" className="nav-link">
                                        <img src={userReduxState.imageUrl} width={25} height={25}
                                                className="rounded-circle"
                                                alt=""/> {userReduxState.username}
                                    </Link>
                                </li>
                            }
                            <li className="nav-item">
                                <Link to="/" className="nav-link text-dark fw-bold"onClick={clickLogOff}>Logout</Link>
                            </li>
                        </> : <li className="nav-item">
                            <Link to="/users/login" className="nav-link text-dark fw-bold">Login</Link>
                        </li>
                    }
                    </ul>
                </div>
                </div>
            </nav>
       </>
    );
}
export default NavBar;
