import React , { useEffect } from 'react';
import NavBar from '../../../layout/NavBar';
import SpinningUI from '../../../layout/SpinningUI';
import LayoutHeading from '../../../layout/LayoutHeading';
import ErrorMessage from '../../../layout/ErrorMessage';
import { Link } from 'react-router-dom';
import { AppDispatch, RootState, useAppDispatch,} from '../../../redux/store';
import {useSelector} from "react-redux";
import * as shoppingActions from "../../../redux/shopping/shopping.actions";
import * as shoppingReducer from "../../../redux/shopping/shopping.reducer";
import * as userReducer from "../../../redux/users/user.reducer";

const AdminPage:React.FC = () => {
  const dispatch:AppDispatch = useAppDispatch();

      //get shopping state from redux

    const shoppingReduxState:shoppingReducer.InitialState = useSelector((state:RootState) => {
      return state[shoppingReducer.shoppingFeatureKey];
    });

    // get user data from redux
    const userReduxState:userReducer.InitialState = useSelector((state:RootState) => {
      return state[userReducer.userFeatureKey];
  });

  const {user} = userReduxState;

  useEffect(() => {
  getAllItemsFromServer();
  },[]);

  const getAllItemsFromServer = () => {
    dispatch(shoppingActions.getAllItemsAction());
  };
  
  const deleteItem = (itemId:string) => {
    if(itemId){
      dispatch(shoppingActions.deleteItemAction({itemId:itemId})).then((response:any) => {
        if(response && !response.error){
          getAllItemsFromServer();
        }
      });
    }
  };
  const {loading,items,error} = shoppingReduxState
  const displayAdminContent = () => {
  return <div className='container'>
         <div className='row'>
          <div className='col'>
            <Link to="/shopping/add"className="btn btn-primary"><i className="bi bi-plus-circle-dotted"></i> New</Link>
             <table className='table table-striped table-hover text-center mt-3'>
              <thead className='bg-dark text-white'>
                <tr>
                  <th>SNO</th>
                  <th>Item Image</th>
                  <th>Name</th>
                  <th>Price</th>
                  <th>Brand</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                  {
                    !loading && items && items.length > 0 && items.map((item,index) => {
                      return (
                        <tr key={item._id}>
                        <td>{index + 1}</td>
                        <td>
                          <img src={item.imageUrl}alt=""width={50} height={50}/>
                        </td>
                        <td>{item.name}</td>
                        <td>{item.price}</td>
                        <td>{item.brand}</td>
                        <td>
                          <Link to={`/shopping/edit/${item._id}`}className='btn btn-success'><i className="bi bi-pencil"></i></Link>
                          <button className='btn btn-danger'onClick={() => deleteItem(`${item._id}`)}><i className="bi bi-trash"></i></button>
                        </td>
                        </tr>
                      )
                    })
                  }
                  {
                        !loading && items.length === 0 &&
                        <tr>
                            <td colSpan={7} className="text-danger">------ No Items Found ------</td>
                        </tr>
                  }
              </tbody>
             </table>
          </div>
        </div>
      </div>
      }
      return(
        <>
          {loading && <SpinningUI/>}
          <NavBar/>
          <LayoutHeading heading={"Item Details"}/>
          {error && <ErrorMessage message={error}/>}
          {user && user.isAdmin ? displayAdminContent() : 
          <div className='container'>
              <div className='row'>
                  <div className='col'>
                      <p className='h3 text-center text-danger mt-5'>------- You are not Authorized access this page -------</p>
                  </div>
              </div>
          </div>}
        </>
      );
}
export default AdminPage;
