import {createAsyncThunk} from "@reduxjs/toolkit";
import { IItemView } from "../../modules/shopping/models/IItemView";
import { ItemService } from "../../modules/shopping/services/ItemService";
import { ICategoryView } from "../../modules/shopping/models/ICategoryView";
import { AuthUtil } from "../../util/AuthUtil";

/**
 * PRIVATE
 */

export const getAllItemsAction: any = createAsyncThunk('shopping/getAllItemsAction',
    async (payload: {}, {rejectWithValue}): Promise<IItemView[] | any> => {
        try {
            if (AuthUtil.isSetTokenToRequestHeader()) {
            let response = await ItemService.getAllItems();
            return response.data;
            }
        } catch (err: any) {
            if (!err.response) {
                throw err
            }
            return rejectWithValue(err.response.data)
        }
    })
    export const getItemAction: any = createAsyncThunk('shopping/getItemAction',
    async (payload: {itemId:string}, {rejectWithValue}): Promise<IItemView | any> => {
        try {
            if (AuthUtil.isSetTokenToRequestHeader()) {
            const {itemId} = payload
            let response = await ItemService.getItem(itemId);
            return response.data;
            }
        } catch (err: any) {
            if (!err.response) {
                throw err
            }
            return rejectWithValue(err.response.data)
        }
    })
