import React from 'react';

interface IProps{
    heading : string;
}

const LayoutHeading:React.FC<IProps> = (props) => {
    return (
        <>
            <div className="container mt-4 pt-5">
                <div className="row">
                    <p className="h3 text-primary">{props.heading}</p>
                    <p className="fst-italic">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Incidunt, 
                    dolores quidem iste ex eos,assumenda animi quae inventore eum excepturi quibusdam odit 
                    veniam quos repellendus tempore quas. Voluptas, fugit maiores?</p>
                </div>
            </div>
        </>
    );
}
export default LayoutHeading;
