import React from 'react';
import { Link } from 'react-router-dom';
import { IItemView } from '../models/IItemView';

interface IProps{
  item : IItemView; 
}

const DisplayItemCard:React.FC<IProps>= (props) => {
  const {item} = props
  return (
    <>
        <div className="card mt-2">
          <div className="card-header text-center">
            <Link to={`/shopping/view/${item._id}`}>
            <img src={item.imageUrl}
            alt=""className="image-fluid w-50 rounded-circle shadow-lg"/>
            </Link>
          </div>
          <div className="card-body">
            <ul className="list-group">
              <li className="list-group-item">
                Name : <span className="fw-bold">{item.name}</span>
              </li>
              <li className="list-group-item">
                Price : <span className="fw-bold">{item.price}</span>
              </li>
              <li className="list-group-item">
                Brand : <span className="fw-bold">{item.brand}</span>
              </li>
            </ul>
          </div>
        </div>
    </>
  );
}
export default DisplayItemCard;
