import React , {useState ,useEffect} from 'react';
import { IItemView } from '../models/IItemView';
import NavBar from '../../../layout/NavBar';
import LayoutHeading from '../../../layout/LayoutHeading';
import {Link , useNavigate} from 'react-router-dom';
import { AppDispatch, RootState, useAppDispatch,} from '../../../redux/store';
import {useSelector} from "react-redux";
import * as shoppingActions from "../../../redux/shopping/shopping.actions";
import * as shoppingReducer from "../../../redux/shopping/shopping.reducer";
import SpinningUI from '../../../layout/SpinningUI';
import ErrorMessage from '../../../layout/ErrorMessage';


const AddItem:React.FC = () => {
  const navigate = useNavigate();
  const dispatch:AppDispatch = useAppDispatch();

   //get shopping state from redux

   const shoppingReduxState:shoppingReducer.InitialState = useSelector((state:RootState) => {
    return state[shoppingReducer.shoppingFeatureKey];
  })

  const {loading,error,categories} = shoppingReduxState

  const [item,setItem] = useState<IItemView>({
    name : "",
    imageUrl : "",
    price : "",
    brand :"",
    mobile: "",
    categoryId : "",
    description : ""
  })

  useEffect(() => {
    dispatch(shoppingActions.getAllCategoriesAction());
  },[]);

  const updateInput = (event:React.ChangeEvent<HTMLInputElement | HTMLSelectElement | any>):void => {
    setItem({
      ...item,
      [event.target.name]:event.target.value
    });
  };
  
  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    dispatch(shoppingActions.createItemAction({item:item})).then((response:any) => {
      if(response && !response.error){
        navigate('/shopping/admin');
      }
    });
  }
  return (
    <>
      {loading && <SpinningUI/>}
      <NavBar/>
      <LayoutHeading heading={"Create Item"}/>
      {error && <ErrorMessage message={error}/>}
      <div className='container'>
        <div className='row'>
          <div className='col-sm-5 mt-2'>
            <div className='card'>
              <div className='card-body'>
              <form onSubmit={e => handleSubmit(e)}>
              <div className='mb-3'>
                <input 
                value={item.name}
                name={"name"}
                onChange={e => updateInput(e)}
                type="text"className='form-control'placeholder='Name'required/>
                </div>
                <div className='mb-3'>
                <input 
                value={item.imageUrl}
                name={"imageUrl"}
                onChange={e => updateInput(e)}
                type="text"className='form-control'placeholder='Image URL'required/>
                </div>
                <div className='mb-3'>
                <input
                value={item.price}
                name={"price"}
                onChange={e => updateInput(e)} 
                type="number"className='form-control'placeholder='Price'required/>
                </div>
                <div className='mb-3'>
                <input
                value={item.brand}
                name={"brand"}
                onChange={e => updateInput(e)} 
                type="text"className='form-control'placeholder='Brand'required/>
                </div>
                <div className='mb-3'>
                <input
                value={item.mobile}
                name={"mobile"}
                onChange={e => updateInput(e)} 
                type="number"className='form-control'placeholder='Mobile'required/>
                </div>
                <div className='mb-3'>
                  <select
                  value={item.categoryId}
                  name={"categoryId"}
                  onChange={e => updateInput(e)} 
                  className='form-control'required>
                    <option value="">Select a category</option>
                    {
                    categories.map((category,index) => {
                      return (
                        <option value={category._id}>{category.name}</option>
                      )
                    })
                    }
                  </select>
                  </div>
                  <div className='mb-3'>
                  <textarea 
                  value={item.description}
                  name={"description"}
                  onChange={e => updateInput(e)}
                  cols={55} rows={2} placeholder='Enter your description'required className='ps-2'></textarea>
                </div>
                <input className="btn btn-success" type="submit"value="Create"/>
                <Link to="/shopping/admin"className="btn btn-danger">Cancel</Link>
                </form>
              </div>
            </div>
          </div>
          <div className='col-sm-3'>
            <img src={item.imageUrl}alt=""className='img-fluid rounded-circle'/>
          </div>
        </div>
      </div>
    </>
  );
}
export default AddItem;
