import React, { useEffect} from 'react';
import { useParams } from 'react-router-dom';
import NavBar from '../../../layout/NavBar';
import LayoutHeading from '../../../layout/LayoutHeading';
import SpinningUI from '../../../layout/SpinningUI';
import ErrorMessage from '../../../layout/ErrorMessage';
import ViewCard from '../components/ViewCard';
import { AppDispatch, RootState, useAppDispatch,} from '../../../redux/store';
import {useSelector} from "react-redux";
import * as shoppingActions from "../../../redux/shopping/shopping.actions";
import * as shoppingReducer from "../../../redux/shopping/shopping.reducer";

const ViewItem:React.FC = () => {
  const {itemId} = useParams(); 

  const dispatch:AppDispatch = useAppDispatch();

      //get shopping state from redux

    const shoppingReduxState:shoppingReducer.InitialState = useSelector((state:RootState) => {
      return state[shoppingReducer.shoppingFeatureKey];
    })

  useEffect(() => {
    if(itemId){
    getItemFromServer(itemId);
    }
  },[itemId])

  const getItemFromServer = (itemId:string) => {
       dispatch(shoppingActions.getItemAction({itemId:itemId}));
  };
  
  useEffect(() => {
      if(item && Object.keys(item).length > 0){
        if(item.categoryId){
          getCategoryInfoFromServer(item.categoryId);
        }
      }
  },[shoppingReduxState.item]);

  const getCategoryInfoFromServer = (categoryId:string) => {
        dispatch(shoppingActions.getCategoryAction({categoryId:categoryId}))
  }

  const {loading,item,category,error} = shoppingReduxState
  return (
    <>
      {loading && <SpinningUI/>}
      <NavBar/>
      {error && <ErrorMessage message={error}/>}
      <LayoutHeading heading={"View Item"}/>
      {
        item && Object.keys(item).length > 0 && category && Object.keys(category).length > 0 &&
        <ViewCard item={item} category={category}/>
      }
    </>
  );
}
export default ViewItem;
