import React, {useEffect, useState} from 'react';
import './App.css';
import NavBar from "./layout/NavBar";
import {BrowserRouter, Routes, Route} from 'react-router-dom';
import HomePage from './modules/ui/pages/HomePage';
import DisplayItems from './modules/shopping/pages/DisplayItems';
import ViewItem from './modules/shopping/pages/ViewItem';
import AdminPage from './modules/shopping/pages/AdminPage';
import AddItem from './modules/shopping/pages/AddItem';
import EditItem from './modules/shopping/pages/EditItem';
import PageNotFound from './modules/ui/pages/PageNotFound';
import ReactToastifyConfiguration from './modules/ui/components/ReactToastifyConfiguration';
import UserLogin from './modules/users/pages/UserLogin';
import UserRegister from './modules/users/pages/UserRegister';
import { AppDispatch, useAppDispatch } from './redux/store';
import { TokenUtil } from './util/TokenUtil';
import * as userActions from "./redux/users/user.actions";
import AdminRoute from './Router/AdminRoute';
import PrivateRoute from './Router/PrivateRoute';

const App: React.FC = () => {
    const dispatch: AppDispatch = useAppDispatch();

    useEffect(() => {
        if (TokenUtil.isLoggedIn()) {
            dispatch(userActions.getUserInfoAction());
        }
    }, [])

    return (
        <>  
            <BrowserRouter>
            <ReactToastifyConfiguration/>
                <Routes>
                    <Route path={'/'}element={<HomePage/>}/>
                    <Route path={'/shopping/display'}element={<DisplayItems/>}/>
                    <Route path={'/shopping/view/:itemId'}element={<ViewItem/>}/>
                    <Route path={'/shopping/admin'}element={
                    <PrivateRoute>
                    <AdminPage/>
                    </PrivateRoute>
                    }/>
                    <Route path={'/shopping/add'}element={
                    <PrivateRoute>
                    <AddItem/>
                    </PrivateRoute>
                    }/>
                    <Route path={'/shopping/edit/:itemId'}element={
                    <PrivateRoute>
                    <EditItem/>
                    </PrivateRoute>
                    }/>
                    <Route path={'/users/login'}element={<UserLogin/>}/>
                    <Route path={'/users/register'}element={<UserRegister/>}/>
                    <Route path={'*'} element={<PageNotFound/>}/>
                </Routes>
            </BrowserRouter>
        </>
    );
}
export default App;
