import React, { useState , useEffect} from 'react';
import { IItemView } from '../models/IItemView';
import { useNavigate , useParams} from 'react-router-dom';
import { Link } from 'react-router-dom';
import NavBar from '../../../layout/NavBar';
import LayoutHeading from '../../../layout/LayoutHeading';
import { AppDispatch, RootState, useAppDispatch,} from '../../../redux/store';
import {useSelector} from "react-redux";
import * as shoppingActions from "../../../redux/shopping/shopping.actions";
import * as shoppingReducer from "../../../redux/shopping/shopping.reducer";
import SpinningUI from '../../../layout/SpinningUI';

const EditItem:React.FC = () => {
  const navigate = useNavigate();

  const {itemId} = useParams();

  const dispatch:AppDispatch = useAppDispatch();

   //get shopping state from redux

   const shoppingReduxState:shoppingReducer.InitialState = useSelector((state:RootState) => {
    return state[shoppingReducer.shoppingFeatureKey];
  })
 
  const {loading,item:itemRedux,error,categories} = shoppingReduxState


  const [itemLocal,setItemLocal] = useState<IItemView>({
    name : "",
    imageUrl : "",
    price : "",
    brand :"",
    mobile: "",
    categoryId : "",
    description : ""
  })

  useEffect(() => {
    getAllCategoriesFromServer();
  },[]);

  const getAllCategoriesFromServer = () => {
    dispatch(shoppingActions.getAllCategoriesAction());
  };

  const updateInput = (event:React.ChangeEvent<HTMLInputElement | HTMLSelectElement | any>):void => {
   setItemLocal({
    ...itemLocal,
    [event.target.name]:event.target.value
   })
  };

  useEffect(() => {
    if(itemId){
    getItemFromServer(itemId);
    }
},[itemId]);

useEffect(() => {
  if(itemRedux && Object.keys(itemRedux).length > 0){
    setItemLocal({
      ...itemLocal,
      name : itemRedux.name,
      imageUrl : itemRedux.imageUrl,
      price : itemRedux.price,
      brand : itemRedux.brand,
      categoryId : itemRedux.categoryId,
      description : itemRedux.description
    })
  }
},[itemRedux])

  const getItemFromServer = (itemId : string) => {
    dispatch(shoppingActions.getItemAction({ itemId:itemId}));
    };
  
  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    dispatch(shoppingActions.updateItemAction({item:itemLocal,itemId:itemId})).then((response:any) => {
    if(response && !response.error){
      navigate('/shopping/admin');
    }
  })
  };
  return (
    <>
      {loading && <SpinningUI/>}
      <NavBar/>
      <LayoutHeading heading={"Edit Item"}/>
      <div className='container'>
        <div className='row'>
          <div className='col-sm-5 mt-2'>
            <div className='card'>
              <div className='card-body'>
              <form onSubmit={e => handleSubmit(e)}>
              <div className='mb-3'>
                <input 
                value={itemLocal.name}
                name={"name"}
                onChange={e => updateInput(e)}
                type="text"className='form-control'placeholder='Name'required/>
                </div>
                <div className='mb-3'>
                <input 
                value={itemLocal.imageUrl}
                name={"imageUrl"}
                onChange={e => updateInput(e)}
                type="text"className='form-control'placeholder='Image URL'required/>
                </div>
                <div className='mb-3'>
                <input
                value={itemLocal.price}
                name={"price"}
                onChange={e => updateInput(e)} 
                type="number"className='form-control'placeholder='Price'required/>
                </div>
                <div className='mb-3'>
                <input
                value={itemLocal.brand}
                name={"brand"}
                onChange={e => updateInput(e)} 
                type="text"className='form-control'placeholder='Brand'required/>
                </div>
                <div className='mb-3'>
                <input
                value={itemLocal.mobile}
                name={"mobile"}
                onChange={e => updateInput(e)} 
                type="number"className='form-control'placeholder='Mobile'required/>
                </div>
                <div className='mb-3'>
                  <select
                  value={itemLocal.categoryId}
                  name={"categoryId"}
                  onChange={e => updateInput(e)} 
                  className='form-control'required>
                    <option value="">Select a category</option>
                    {
                    categories.map((category,index) => {
                      return (
                        <option value={category._id}>{category.name}</option>
                      )
                    })
                    }
                  </select>
                  </div>
                  <div className='mb-3'>
                  <textarea 
                  value={itemLocal.description}
                  name={"description"}
                  onChange={e => updateInput(e)}
                  cols={55} rows={2} placeholder='Enter your description'required></textarea>
                </div>
                <input className="btn btn-success" type="submit"value="Update"/>
                <Link to="/shopping/admin"className="btn btn-danger">Cancel</Link>
                </form>
              </div>
            </div>
          </div>
          <div className='col-sm-3'>
            <img src={itemLocal.imageUrl}alt=""className='img-fluid rounded-circle'/>
          </div>
        </div>
      </div>
    </>
  );
}
export default EditItem;
