import React from 'react';
// import {useLocation} from "react-router-dom";
import { Link } from 'react-router-dom';
import imageLogo from "F:/React-JS-Typescript/first-app/src/assets/img/ecommerce-website-logo.png";
import NavBar from '../../../layout/NavBar';
import { TokenUtil } from '../../../util/TokenUtil';
const HomePage: React.FC = () => {
    // let location = useLocation();

    return (
        <>  
            <NavBar/>
            {/* <pre>{JSON.stringify(location)}</pre> */}
            <div className="shopping-landing-page">
                <div className="wrapper">
                    <div className="d-flex justify-content-center align-items-center text-center h-100 flex-column text-white">
                    <div className="d-flex flex-row">
                        <img src={imageLogo}width={200}height={100}/>
                    <p className="display-2 text-white fst-italic mt-2">Get Shopping</p>
                    </div>
                    <p className="text">Lorem ipsum dolor sit amet consectetur, adipisicing elit. 
                    Illo ea vero sequi labore minima, expedita numquam, tenetur rem tempora voluptas quam 
                    quisquam velit iusto amet at, perferendis temporibus repudiandae maiores.</p>
                    <div>
                        {
                        TokenUtil.isLoggedIn() ?
                        <Link className="btn btn-primary" to="/shopping/display">Go Shopping</Link> :
                        <Link className="btn btn-success" to="/users/login">Login</Link>
                        }
                    </div>
                    </div>
                </div>
            </div>
        </>
    );
}
export default HomePage;
