import React from 'react';
import { IItemView } from '../models/IItemView';
import { Link } from 'react-router-dom';
import { ICategoryView } from '../models/ICategoryView';

interface IProps{
  item : IItemView;
  category :ICategoryView;
}

const ViewCard:React.FC<IProps>= (props) => {
  const {item,category} = props
  return (
    <>
     <div className="container mt-3">
        <div className="row">
          <div className="col-sm-3 mt-2">
          <img src={item.imageUrl}
          alt=""className="image-fluid w-75 rounded-circle shadow-lg"/>
          <div className="mt-2">
            <Link to="/shopping/display"className="btn btn-danger"><i className="bi bi-arrow-left-circle me-1"></i>Back</Link>
          </div>
          </div>
          <div className="col-sm-7 offset-1 mt-2">
              <ul className="list-group">
                <li className="list-group-item">
                  Name : <span className="fw-bold">{item.name}</span>
                </li>
                <li className="list-group-item">
                  ImageUrl : <span className="fw-bold">{item.imageUrl}</span>
                </li>
                <li className="list-group-item">
                  Price : <span className="fw-bold">{item.price}</span>
                </li>
                <li className="list-group-item">
                  Brand : <span className="fw-bold">{item.brand}</span>
                </li>
                <li className="list-group-item">
                  Description : <span className="fw-bold">{item.description}</span>
                </li>
                <li className="list-group-item">
                  Category: <span className="fw-bold">{category.name}</span>
                </li>
              </ul>
          </div>
        </div>
      </div>
    </>
  );
}
export default ViewCard;
